import React from 'react';
import Button from '@material-ui/core/Button';

import { connect } from "react-redux";
import {setValue} from "../../src/actions";

import io from "socket.io-client";

class GenericButton extends React.Component {
  
  constructor(props) {
    super(props);
    this.run = this.run.bind(this);
  }

  run() {

    //change this to send different data
    if(this.props.message == "SET")
    {
      let dataToSend = 'SET_exp' + this.props.exp + '_' + '_game' + this.props.game + '_'
    }else
    {
      let dataToSend = this.props.message
    }
    
    
    console.log(dataToSend)
    this.props.socket.emit('MESSAGE',dataToSend)
    //this.props.setValue('home', 1)
  
  }

  render() {

    return (
      <div style={{padding: 12}}>
      <Button variant="contained" color="primary" onClick={this.run}>
        {this.props.text}
      </Button>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    socket: state.socket
  }
}

export default connect(
  mapStateToProps,
  { setValue }
)(GenericButton);

